/* Box sizing reset */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin and padding */
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
  padding: 0;
}

/* Remove list styles on ul, ol elements with a list role */
ul[role=list],
ol[role=list],
ul,
ol {
  list-style: none;
}

/* Set default font smoothing */
html {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
}

/* Remove default styles from links */
a {
  text-decoration: none;
  color: inherit;
}

/* Remove all borders and background from buttons */
button {
  all: unset;
  cursor: pointer;
}

/* Remove built-in form styles */
input,
textarea,
select {
  font: inherit;
  color: inherit;
  border: none;
  outline: none;
  background: none;
}

/* Images behave consistently */
img,
picture,
svg {
  display: block;
  max-width: 100%;
  height: auto;
}

/* Set body defaults */
body {
  min-height: 100vh;
  line-height: 1.5;
  font-family: system-ui, sans-serif;
  background-color: #0f0f0f;
  color: #fff;
}